.dsm-c-search-input{height:36px;position:relative}.dsm-c-search-input input{box-sizing:border-box;width:100%;height:100%;font-size:14px;color:#2e2f30;border:0;border-bottom:1px solid #dcddde;outline:none;padding:0 30px}.dsm-c-search-input input::-webkit-input-placeholder{color:#b8bbbf}.dsm-c-search-input input::-moz-placeholder{color:#b8bbbf}.dsm-c-search-input input:-ms-input-placeholder{color:#b8bbbf}.dsm-c-search-input input:focus{border-color:#418fd7}.dsm-c-search-input .dsm-c-disabled{cursor:default;color:#dcddde;background-color:inherit;pointer-events:none}.dsm-c-search-input .dsm-c-disabled:focus{border-color:#aaa!important}.dsm-c-search-input .dsm-c-icon{position:absolute;line-height:normal;top:10px}.dsm-c-search-input .icon-_search{left:4px;cursor:default}.dsm-c-search-input .icon-_close{right:4px}