.dsm-c-checkbox{display:inline-block;position:relative;cursor:default;user-select:none;height:17px;line-height:17px}.dsm-c-checkbox.disabled-checkbox{opacity:.3}.dsm-c-checkbox.dsm-c-checkbox-dark .checkbox-viewer{background:#283142;color:#eef0f4;border-color:#42516c}.dsm-c-checkbox .dsm-c-link{vertical-align:middle;margin-left:6px}.dsm-c-checkbox .checkbox-text,.dsm-c-checkbox .checkbox-viewer{vertical-align:middle}.dsm-c-checkbox .checkbox-viewer{display:inline-block;flex:14px 0 0;width:14px;height:14px;border:1px solid #696b6c;background-color:#fff}.dsm-c-checkbox .checkbox-viewer.checkbox-checked{position:relative;background-color:#696b6c;text-align:center}.dsm-c-checkbox .checkbox-viewer.checkbox-checked i{position:absolute;top:0;left:0;line-height:normal}.dsm-c-checkbox .checkbox-checked-indeterminate{position:relative;background-color:#696b6c}.dsm-c-checkbox .checkbox-checked-indeterminate:before{content:"";position:absolute;top:50%;left:50%;width:60%;height:2px;transform:translate(-50%,-50%);background-color:#fff}.dsm-c-checkbox .checkbox-text{margin-left:10px;font-size:14px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap}.dsm-c-checkbox .dsc{color:#969798;font-size:1.2rem;margin-top:8px}