.dsm-c-input{width:140px;height:36px;position:relative;display:inline-block}.dsm-c-input input{box-sizing:border-box;width:100%;height:100%;font-size:14px;color:#2e2f30;border:0;border-bottom:1px solid #dcddde;outline:none;padding:0 4px}.dsm-c-input input::-webkit-input-placeholder{color:#dcddde}.dsm-c-input input::-moz-placeholder{color:#dcddde}.dsm-c-input input:-ms-input-placeholder{color:#dcddde}.dsm-c-input input:focus,.dsm-c-input input:hover{border-color:#418fd7}.dsm-c-input.dsm-c-input-error input,.dsm-c-input.dsm-c-input-error span{color:#fe4066}.dsm-c-input.dsm-c-input-error input{border-color:#418fd7}.dsm-c-input.dsm-c-input-error span{position:absolute;top:46px;left:4px;font-size:1.2rem}.dsm-c-input.dsm-c-input-error .ds-c-input-error-msg{line-height:22px}